/**
 * AIdeas - UI Loader Intelligente
 * Gestisce il caricamento dell'interfaccia con fallback automatico
 */

import StorageService from './services/StorageService.js';
import { showToast } from './utils/helpers.js';

/**
 * Classe per gestire il caricamento intelligente dell'UI
 */
class UILoader {
  constructor() {
    this.uiMode = 'auto';
    this.fallbackAttempted = false;
    this.loadingTimeout = null;
  }

  /**
   * Inizializza il caricamento dell'interfaccia
   */
  async init() {
    try {
      console.log('üé® Inizializzazione UI Loader...');
      
      // Inizializza storage
      await StorageService.ensureDbOpen();
      
      // Carica impostazione UI mode
      this.uiMode = await StorageService.getSetting('uiMode', 'auto');
      console.log(`üì± Modalit√† UI configurata: ${this.uiMode}`);
      
      // Determina quale UI caricare
      const targetUI = await this.determineTargetUI();
      
      // Carica l'interfaccia appropriata
      await this.loadUI(targetUI);
      
    } catch (error) {
      console.error('‚ùå Errore inizializzazione UI Loader:', error);
      // Fallback di emergenza su vanilla
      await this.loadVanillaUI();
    }
  }

  /**
   * Determina quale interfaccia caricare basandosi sulle impostazioni
   */
  async determineTargetUI() {
    switch (this.uiMode) {
      case 'vanilla':
        return 'vanilla';
      case 'material':
        return 'material';
      case 'auto':
      default:
        return await this.detectBestUI();
    }
  }

  /**
   * Rileva automaticamente la migliore UI da utilizzare
   */
  async detectBestUI() {
    console.log('üîç Rilevamento automatico UI...');
    
    // Controlla se Material UI √® supportato
    const materialSupported = await this.checkMaterialUISupport();
    
    if (materialSupported) {
      console.log('‚úÖ Material UI supportato, tentativo di caricamento...');
      return 'material';
    } else {
      console.log('‚ö†Ô∏è Material UI non supportato, uso vanilla');
      return 'vanilla';
    }
  }

  /**
   * Verifica se Material UI √® supportato dal browser
   */
  async checkMaterialUISupport() {
    try {
      // Controlla supporto ES6 modules
      if (!window.import) {
        console.log('‚ùå ES6 modules non supportati');
        return false;
      }

      // Controlla se React √® disponibile
      const reactCheck = await this.testModuleImport('react');
      if (!reactCheck) {
        console.log('‚ùå React non disponibile');
        return false;
      }

      // Controlla se Material UI √® disponibile
      const muiCheck = await this.testModuleImport('@mui/material');
      if (!muiCheck) {
        console.log('‚ùå Material UI non disponibile');
        return false;
      }

      return true;
    } catch (error) {
      console.log('‚ùå Errore verifica Material UI:', error);
      return false;
    }
  }

  /**
   * Testa l'importazione di un modulo
   */
  async testModuleImport(moduleName) {
    try {
      // Timeout per evitare blocchi
      const timeoutPromise = new Promise((_, reject) => {
        setTimeout(() => reject(new Error('Timeout')), 3000);
      });

      const importPromise = import(moduleName);
      await Promise.race([importPromise, timeoutPromise]);
      
      return true;
    } catch (error) {
      return false;
    }
  }

  /**
   * Carica l'interfaccia specificata
   */
  async loadUI(targetUI) {
    console.log(`üöÄ Caricamento UI: ${targetUI}`);
    
    try {
      if (targetUI === 'material') {
        await this.loadMaterialUI();
      } else {
        await this.loadVanillaUI();
      }
    } catch (error) {
      console.error(`‚ùå Errore caricamento ${targetUI} UI:`, error);
      
      // Se √® il primo tentativo e stiamo caricando Material UI, prova fallback
      if (!this.fallbackAttempted && targetUI === 'material') {
        console.log('üîÑ Tentativo fallback su vanilla...');
        this.fallbackAttempted = true;
        await this.handleFallback();
      } else {
        // Fallback di emergenza
        await this.loadVanillaUI();
      }
    }
  }

  /**
   * Carica l'interfaccia Material UI
   */
  async loadMaterialUI() {
    console.log('üé® Caricamento Material UI...');
    
    // Controlla se √® gi√† stato inizializzato
    if (window.aideasMaterialUIInitialized) {
      console.log('‚ö†Ô∏è Material UI gi√† inizializzato, skip');
      return;
    }
    
    // Timeout per evitare blocchi
    this.loadingTimeout = setTimeout(() => {
      throw new Error('Timeout caricamento Material UI');
    }, 10000);

    try {
      console.log('üì¶ Importazione modulo Material UI...');
      // Carica il modulo Material UI
      const { initializeAIdeasWithMaterialUI } = await import('./main-material.jsx');
      
      console.log('üîß Funzione di inizializzazione ottenuta:', typeof initializeAIdeasWithMaterialUI);
      
      // Inizializza l'app
      console.log('üöÄ Avvio inizializzazione Material UI...');
      initializeAIdeasWithMaterialUI();
      
      // Marca come inizializzato
      window.aideasMaterialUIInitialized = true;
      
      // Cancella timeout
      clearTimeout(this.loadingTimeout);
      
      console.log('‚úÖ Material UI caricata con successo');
      
      // Nascondi il loading screen
      this.hideLoadingScreen();
      
    } catch (error) {
      clearTimeout(this.loadingTimeout);
      console.error('‚ùå Errore durante caricamento Material UI:', error);
      throw error;
    }
  }

  /**
   * Carica l'interfaccia Vanilla
   */
  async loadVanillaUI() {
    console.log('üé® Caricamento Vanilla UI...');
    
    try {
      // Carica il modulo vanilla
      const { default: AIdeasApp } = await import('./main.js');
      
      // Inizializza l'app
      const app = new AIdeasApp();
      
      // Rendi globale per debugging
      window.aideasApp = app;
      
      // Avvia applicazione
      await app.init();
      
      console.log('‚úÖ Vanilla UI caricata con successo');
      
      // Nascondi il loading screen
      this.hideLoadingScreen();
      
    } catch (error) {
      console.error('‚ùå Errore caricamento Vanilla UI:', error);
      throw error;
    }
  }

  /**
   * Gestisce il fallback da Material UI a Vanilla
   */
  async handleFallback() {
    try {
      console.log('üîÑ Esecuzione fallback su Vanilla UI...');
      
      // Aggiorna impostazione per evitare futuri tentativi
      await StorageService.setSetting('uiMode', 'vanilla');
      
      // Mostra notifica all'utente
      showToast('Material UI non disponibile, passaggio a interfaccia classica', 'info');
      
      // Carica vanilla
      await this.loadVanillaUI();
      
    } catch (error) {
      console.error('‚ùå Errore durante fallback:', error);
      // Ultimo tentativo di caricamento vanilla
      await this.loadVanillaUI();
    }
  }

  /**
   * Nasconde il loading screen
   */
  hideLoadingScreen() {
    try {
      const loadingScreen = document.getElementById('loading-screen');
      if (loadingScreen) {
        loadingScreen.style.display = 'none';
        console.log('üéØ Loading screen nascosto');
      }
    } catch (error) {
      console.error('‚ùå Errore nascondere loading screen:', error);
    }
  }

  /**
   * Cambia modalit√† UI (per uso esterno)
   */
  async changeUIMode(newMode) {
    try {
      console.log(`üîÑ Cambio modalit√† UI da ${this.uiMode} a ${newMode}`);
      
      // Salva nuova impostazione
      await StorageService.setSetting('uiMode', newMode);
      this.uiMode = newMode;
      
      // Ricarica la pagina per applicare il cambio
      window.location.reload();
      
    } catch (error) {
      console.error('‚ùå Errore cambio modalit√† UI:', error);
      showToast('Errore durante il cambio di interfaccia', 'error');
    }
  }
}

// Esporta per uso esterno
window.UILoader = UILoader;

// Auto-inizializzazione
const uiLoader = new UILoader();

// Inizializza quando il DOM √® pronto
if (document.readyState === 'loading') {
  document.addEventListener('DOMContentLoaded', () => uiLoader.init());
} else {
  uiLoader.init();
}

export default UILoader; 